<template>
  <div class="header">
      <ul class="header-button-left">
        <li>Cancel</li>
      </ul>
      <ul class="header-button-right">
        <li v-if="step == 1" @click="step++">Next</li>
        <!-- step == 2일 때만 나타남 -->
        <li v-if="step == 2" @click="publish">발행</li>
      </ul>
      <img src="./assets/logo.png" class="logo" />
    </div>

    <ContainerComp @write="contents = $event" :data="data" :step="step" :postim="postim" />
    <button @click="more">더보기</button>

    <!-- 이미지 업로드버튼 만들기 -->
    <div class="footer">
      <ul class="footer-button-plus">
        <input @change="upload" accept="image/*" multiple type="file" id="file" class="inputfile" />
        <label for="file" class="input-plus">+</label>
      </ul>
  </div>

  <!-- 탭 만들기 -->
  <div v-if="step == 0">내용0</div>
  <div v-if="step == 1">내용1</div>
  <div v-if="step == 2">내용2</div>
  <button @click="step = 0">버튼0</button>
  <button @click="step = 1">버튼1</button>
  <button @click="step = 2">버튼2</button>
  <div style="margin-bottom:300px;"></div>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */

import ContainerComp from './components/ContainerComp.vue';
import postdata from './assets/postdata'
import axios from 'axios'

export default {
  name: 'App',
  data(){
    return {
    count: 0,
    step: 0, //현재 페이지 상태
    data: postdata, //게시물 [{},{},{},{}]
    postim: '', //이미지
    contents: '', //작성한글
    image: '',
    posting: '' //
    }
  },
  components: {
    ContainerComp
  },
  methods: {
    publish(){
      var mydata = {
        name: "Kim Hyun",
        userImage: "https://picsum.photos/100?random=1",
        postImage: this.postim, //내가업로드한이미지URL
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.contents, //내가 입력한글
        filter: "perpetua"
      };
      this.data.unshift(mydata); //unshift array에 자료 집어넣어줌
      console.log("mydata는", mydata)
      this.step = 0;
    },
    more(){
      // axios.post('URL', {name : 'kim'}).then('성공했을 때').catch((err)=>{
      //   err
      // })
      axios.get(`https://codingapple1.github.io/vue/more${this.count}.json`)
      .then( result => {
        console.log(result.data);
        this.data.push(result.data); //push 
        this.count++;
      })
    },
    upload(e){ //파일업로드시 코드 실행
       let file = e.target.files;
       console.log(file[0].type);
       let url = URL.createObjectURL(file[0]);
       console.log(url); //background 이미지 url
       this.postim = url; //postim에 url 저장
       this.step++;
    }

  }
}
</script>

